import React, {Fragment} from "react";
import {Link} from "react-router-dom";
import EventAggregator from 'ezyvuecore/core/events/eventAggregator';
import AuthManager, { LoggedInEvent } from 'ezyvuecore/core/auth/authManager';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import { Permissions } from 'ezyvuecore/models/user';
import Services from "ezyvuecore/services/services";

export default class LoginButton extends React.Component
{
    constructor() {
        super();
        this.state={
            loggedIn: AuthManager.loggedIn,
            name: '',
            anchorElem: null,
            hasAdminAccess: false
        }
    
        this.onLogout = this.onLogout.bind(this);
        this.onMenuButtonClick = this.onMenuButtonClick.bind(this);
        this.onMenuClose = this.onMenuClose.bind(this);
    }

    async componentDidMount() {
        EventAggregator.instance.subscribe(LoggedInEvent, () => {
            this.setUserLoggedIn();
        });

        this.setUserLoggedIn();
    }

    async setUserLoggedIn() {
        const cookie = AuthManager.getAuthCookie();
        if(cookie) {
            const perm = cookie.permissions.find(perm => perm.PermissionId === Permissions.ADMIN_ACCESS);
            
            const plan = await Services.planService.getById(cookie.planId);
            if(!plan)
                throw new Error('plan not found');

            this.setState({
                hasAdminAccess: perm && Boolean(perm.Granted) && plan.features.teamManagement,
                loggedIn: true,
                name: cookie.firstName
            });
        }
    }

    componentWillUnmount() {
        EventAggregator.instance.unsubscribe(LoggedInEvent, this);
    }

    onLogout() {
        AuthManager.signOut();
        
        window.location = '/account/login';
    }
    
    onMenuButtonClick(e) {
        this.setState({anchorElem: e.currentTarget});
    }

    onMenuClose() {
        this.setState({anchorElem: null});
    }

    render() {
        if(this.state.loggedIn) {
            return (
                <div>
                    <AccountCircleIcon className='icon-fill-color' fontSize='large' onClick={this.onMenuButtonClick}></AccountCircleIcon>
                    <Menu
                        anchorEl={this.state.anchorElem}
                        anchorOrigin={{
                            vertical: 'top',
                            horizontal: 'left',
                        }}
                        keepMounted
                        transformOrigin={{
                            vertical: 'top',
                            horizontal: 'left',
                        }}
                        open={Boolean(this.state.anchorElem)}
                        onClose={this.onMenuClose}
                        >
                        <MenuItem onClick={this.onLogout}>
                            <Link to=''>
                                Sign Out
                            </Link>
                        </MenuItem>
                        <MenuItem onClick={this.onMenuClose}>
                            <Link to='/account'>
                                Account
                            </Link>
                        </MenuItem>
                        {
                            this.state.hasAdminAccess &&
                            <MenuItem onClick={this.onMenuClose}>
                                <Link to='/admin/'>
                                    Team Admin
                                </Link>
                            </MenuItem>
                        }
                    </Menu>                 
                </div>
            );
        }
        else {
            return (
                <Fragment>
                    <Link to="/pricing" className='signup-button'>SIGN&nbsp;UP</Link>
                    <Link to="/account/login">Sign&nbsp;In</Link>
                </Fragment>
            );
        }
    }
}