export const UserRoles = {
  USER: 1,
  DESIGNER: 2,
  ADMINISTRATOR: 3,
  WEBSITE_ADMINISTRATOR: 4,
  WEBSITE_OWNER: 5,
  COUNT: 5,
};

export const Permissions = {
  MEMBER_ACCESS: 1,
  CREATE_UPDATE_ASSET: 2,
  DELETE_ASSET: 3,              // DEPRECATED
  ADMIN_ACCESS: 4,
  MASTER_ADMIN_ACCESS: 10,
  COUNT: 5,
};

export class User {
  constructor(FirstName, LastName, Email, Password, LastAccessed, StoreAccountId, RoleIdCode, PrimaryAccountHolder) {
    this.FirstName = FirstName;
    this.LastName = LastName;
    this.Email = Email;
    this.Password = Password;
    this.LastAccessed = LastAccessed;
    this.StoreAccountId = StoreAccountId;
    this.RoleIdCode = RoleIdCode;
    this.PrimaryAccountHolder = PrimaryAccountHolder;
  }
}

// Permission value for a given role in a store account
export class UserRolePermission {
  constructor(UserRoleId, PermissionId, StoreAccountId, Granted) {
    this.UserRoleId = UserRoleId;
    this.PermissionId = PermissionId;
    this.StoreAccountId = StoreAccountId; // Store Account
    this.Granted = Granted;
  }
}
