import React, { Fragment } from 'react';
import LoginButton from "./loginButton"
import AuthManager, { LoggedInEvent, LoggedOutEvent } from 'ezyvuecore/core/auth/authManager';
import EventAggregator from 'ezyvuecore/core/events/eventAggregator';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import { Permissions } from 'ezyvuecore/models/user';
import Services from 'ezyvuecore/services/services';
import MainMenu from './mainMenu';

export default class Header extends React.Component {
  constructor() {
    super();

    this.state = {
      isMobile: false,
      isLoggedIn: AuthManager.loggedIn,
      anchorElem: null,
      canUpload: true,
      productCatalogAccess: false
    };

    this.onMenuButtonClick = this.onMenuButtonClick.bind(this);
    this.onMediaQueryChanged = this.onMediaQueryChanged.bind(this);
    this.onMenuClose = this.onMenuClose.bind(this);
  }

  async componentDidMount() {
    // detect mobile/desktop
    this.mql = window.matchMedia(`(max-width: 1024px)`);
    this.mql.addListener(this.onMediaQueryChanged);

    this.setState({isMobile: this.mql.matches});

    // set menu permissions
    let editPerm = true;
    let productCatalogAccess = false;

    const cookie = AuthManager.getAuthCookie();
    if (cookie) {
      editPerm = cookie.permissions.find(perm => perm.PermissionId === Permissions.CREATE_UPDATE_ASSET);

      const plan = await Services.planService.getById(cookie.planId);
      if (!plan)
        throw new Error('plan not found');

      productCatalogAccess = plan.features.productCatalog;
    }

    this.setStateFromCookie();

    EventAggregator.instance.subscribe(LoggedOutEvent, () => {
      this.setState({isLoggedIn: false});
    });

    EventAggregator.instance.subscribe(LoggedInEvent, () => {
      this.setStateFromCookie();
    });

    this.setState({
      canUpload: editPerm && editPerm.Granted,
      productCatalogAccess: productCatalogAccess
    });
  }

  componentWillUnmount() {
    EventAggregator.instance.unsubscribe(LoggedOutEvent, this);
    EventAggregator.instance.unsubscribe(LoggedInEvent, this);

    if (this.mql)
      this.mql.removeListener(this.mediaQueryChanged);
  }

  setStateFromCookie() {
    const cookie = AuthManager.getAuthCookie();

    this.setState({isLoggedIn: Boolean(cookie)});
  }

  onMediaQueryChanged() {
    this.setState({ isMobile: this.mql.matches });
  }

  onMenuButtonClick(e) {
    this.setState({ anchorElem: e.currentTarget });
  }

  onMenuClose(e) {
    this.setState({ anchorElem: null });
  }

  render() {
    return (
      <Fragment>
        <div className='header-container'>
          {
            this.state.isMobile &&
            <IconButton edge="start" color="inherit" aria-label="menu" onClick={this.onMenuButtonClick}>
              <MenuIcon className='icon-fill-color' fontSize='large' />
            </IconButton>
          }
          <div className='header-logo'>
            <a href='/'>
              <img alt='' width='37' height='28' src={require('../../assets/images/logo.png')} />
            </a>
          </div>
          <MainMenu
            anchorElem={this.state.anchorElem}
            isMobile={this.state.isMobile}
            loggedIn={this.state.isLoggedIn}
            showProductCatalogItem={this.state.productCatalogAccess}
            showIntegrateItem={this.state.canUpload} 
            onMenuClose={this.onMenuClose}
            />
        </div>
      </Fragment>
    );
  }
}
